import React, { Component } from "react";
import { Route } from "react-router-dom";
import { signin } from "../auth/serverMethods";

import Loginpage from "./LoginPage/LoginPage";
import SignUp from "./SignupPage/Signup";
import Packages from "./Packages/Packages";
import AdminDashboard from "../users/admin/AdminDashboard";
import AccountantDashboard from "../users/accountant/AccountantDashboard";
import UploaderDashboard from "../users/uploader/UploaderDashboard";
import CreatorDashboard from "../users/creator/CreatorDashboard";
import SubscribedUserDashboard from "../users/User/SubscribedUserDashboard";
import ExamList from "../ExamComponents/ExamList";
import TestList from "../ExamComponents/TestList";
import Referral from "./Referral/Referral";
import Help from "./Help/Help";
import FAQs from "./FAQs/FAQs";
import QuizList from "../QuizComponent/QuizList";
import TimerTimer from "../QuizComponent/Timer";

class Routes extends Component {
  state = { userId: null };

  componentDidMount() {
    const user = signin();
    if (user) {
      this.setState({ userId: user.id });
    }
  }

  render() {
    var content = null;
    const userId = this.state.userId;
    if (userId) {
      switch (userId) {
        case "creator":
          content = <Route path="/" component={CreatorDashboard} />;
          break;
        case "accountant":
          content = <Route path="/" component={AccountantDashboard} />;
          break;
        case "uploader":
          content = <Route path="/" component={UploaderDashboard} />;
          break;
        case "admin":
          content = (
            <div>
              <Route path="/" exact component={SubscribedUserDashboard} />
              <Route path="/login" exact component={Loginpage} />
              <Route path="/signUp" exact component={SignUp} />
              <Route path="/packages/:packageId" component={Packages} />
              <Route path="/admin/dashboard" exact component={AdminDashboard} />
            </div>
          );
          break;
        default:
          content = (
            <div>
              <Route path="/" exact component={SubscribedUserDashboard} />
              <Route path="/login" exact component={Loginpage} />
              <Route path="/signUp" exact component={SignUp} />
              <Route path="/exam" exact component={ExamList} />
              <Route path="/test" exact component={TestList} />
              <Route path="/quiz" exact component={QuizList} />
              <Route path="/referral" exact component={Referral} />
              <Route path="/help" exact component={Help} />
              <Route path="/FAQs" exact component={FAQs} />
              <Route path="/timer" exact component={TimerTimer} />
              <Route path="/packages/:packageId" component={Packages} />
            </div>
          );
      }
    }
    return <div>{content}</div>;
  }
}

export default Routes;
