import React, { Component } from "react";
import { API } from "../config";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import coverPic1 from "../assets/course-image-1.jpg";
import TimerTimer from "./Timer";

class QuizList extends Component {
  state = {
    allQuizInfo: null,
    showQuizStartButton: true,
    showQuizInfo: false,
    showQuizQuestions: false,
    count: 60,
    timerOff: true
  };

  componentDidMount() {
    fetch(`${API}/quiz_info`)
      .then(response => response.json())
      .then(data => {
        this.setState({
          allQuizInfo: data
        });
      });
  }
  quizInfo = null;
  questions = null;

  getSelectedQuizInfo = quizName => {
    const { allQuizInfo } = this.state;
    if (allQuizInfo) {
      return allQuizInfo.map(data => {
        if (Object.keys(data)[0] === quizName) {
          this.quizInfo = data[Object.keys(data)];
          this.setState({ showQuizStartButton: false, showQuizInfo: true });
        }
      });
    }
  };

  startQuizHandler = questions => {
    this.setState({
      showQuizQuestions: true,
      showQuizInfo: false,
      count: 60,
      timerOff: false
    });
  };

  endQuizHandler = () => {
    this.quizInfo.shift();
    this.setState({
      showQuizQuestions: false,
      showQuizInfo: true,
      timerOff: true
    });
  };

  callbackFunction = childData => {
    this.setState({ timerOff: childData });
  };

  render() {
    var content = null;
    if (this.state.showQuizStartButton) {
      content = (
        <Button
          onClick={this.getSelectedQuizInfo.bind(this, "REACT_JAVASCRIPT_QUIZ")} //replace this hard coded value as prop from the link this quiz is called
        >
          Show Quiz
        </Button>
      );
    }

    if (this.state.showQuizInfo && this.quizInfo.length > 0) {
      var info = this.quizInfo[0][Object.keys(this.quizInfo[0])];
      content = (
        <Card style={{ width: "18rem" }}>
          <Card.Img variant="top" src={coverPic1} />
          <Card.Body>
            <Card.Title>{info.quizTitle}</Card.Title>
            <Card.Text>{info.quizSynopsis}</Card.Text>
            <Card.Text>Time : {info.timeAlloted} mins</Card.Text>
            <Button
              variant="primary"
              onClick={this.startQuizHandler.bind(this, info.questions)}
            >
              Start quiz
            </Button>
          </Card.Body>
        </Card>
      );
    }

    if (this.state.showQuizInfo && this.quizInfo.length <= 0) {
      content = <Button>Evaluate Quiz</Button>;
    }

    if (
      this.state.showQuizQuestions &&
      this.quizInfo.length > 0 &&
      !this.state.timerOff
    ) {
      var quiz_info = this.quizInfo[0][Object.keys(this.quizInfo[0])];
      this.questions = quiz_info.questions;
      content = (
        <div>
          <div>
            <TimerTimer
              parentCallback={this.callbackFunction}
              time={this.state.count}
              questions={this.questions}
            />
          </div>
        </div>
      );
    }

    if (
      this.state.showQuizQuestions &&
      this.quizInfo.length > 0 &&
      this.state.timerOff
    ) {
      content = <Button onClick={this.endQuizHandler}>Next part</Button>;
    }

    return <div>{content}</div>;
  }
}

export default QuizList;
