import React, { Component } from "react";
import PackageVideo from "./PackageVideo";
import PackageOverview from "./PackageOverview";
import { API } from "../../config";
import NavigationBar from "../NavigationBar/NavigationBar";

class Packages extends Component {
  state = { data: null };

  componentDidMount() {
    const courseId = this.props.match.params.packageId;
    fetch(`${API}/courses/${courseId}`)
      .then(response => response.json())
      .then(data => {
        this.setState({ data });
      });
  }

  render() {
    var content = null;
    var courseDetails = this.state.data;

    if (courseDetails) {
      if (courseDetails.subscribed) {
        content = <PackageVideo data={courseDetails} />;
      } else {
        content = <PackageOverview data={courseDetails} />;
      }
    }
    return (
      <span>
        <NavigationBar />
        {content}
      </span>
    );
  }
}

export default Packages;
