import React, { Component } from "react";

class QuizQuestions extends Component {
  constructor(props) {
    super(props);
    this.state = {
      i: 0,
      correct: 0
    };
    this.questions = this.props[Object.keys(this.props)];
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
  }

  next = () => {
    var { i, correct } = this.state;
    var radioBtn = document.querySelector("input[name='option']:checked");

    if (radioBtn != null) {
      if (this.questions[i].correctAnswer === radioBtn.value) {
        this.setState({ correct: ++correct });
      }
    }
    if (i === this.questions.length - 1) {
      alert(
        "You have answered " +
          this.state.correct +
          " questions out of " +
          this.questions.length +
          " questions correctly"
      );
    } else {
      i++;
      this.setState({ i });
    }
  };
  previous = () => {
    var { i } = this.state;
    if (i > 0) {
      i--;
    }
    this.setState({ i });
  };

  render() {
    const { i } = this.state;
    return (
      <div className="col-md-12">
        <div className="col" id="content">
          <div id="quizContainer">
            <div className="modal-header">
              <h5>
                <i className="fa fa-question-circle" />
                <span> </span>
                <span className="label label-warning">
                  {i + 1} : {this.questions[i].question}
                </span>
              </h5>
            </div>
            <div className="modal-body">
              <div className="quiz" id="quiz" data-toggle="buttons">
                <label className="btn btn-lg btn-info btn-block">
                  <span className="btn-label">
                    <input type="radio" name="option" value="1" />
                    <br />
                    <i className="fa fa-arrow-right" />
                  </span>
                  <span>A. {this.questions[i].answers[0]}</span>
                </label>
                <label className="btn btn-lg btn-info btn-block">
                  <span className="btn-label">
                    <input type="radio" name="option" value="2" />
                    <br />
                    <i className="fa fa-arrow-right" />
                  </span>
                  <span>B. {this.questions[i].answers[1]}</span>
                </label>
                <label className="btn btn-lg btn-info btn-block">
                  <span className="btn-label">
                    <input type="radio" name="option" value="3" />
                    <br />
                    <i className="fa fa-arrow-right" />
                  </span>
                  <span>C. {this.questions[i].answers[2]}</span>
                  <label className="btn btn-lg btn-info btn-block">
                    <span className="btn-label">
                      <input type="radio" name="option" value="4" />
                      <br />
                      <i className="fa fa-arrow-right" />
                    </span>
                    <span>D. {this.questions[i].answers[3]}</span>
                  </label>
                  {/* <button
                    className="btn btn-success pull-right"
                    onClick={this.previous.bind(this)}
                  >
                    Previous question <i className="fa fa-angle-double-right" />
                  </button> */}
                  {/*Need to introduce logic to check answers only when submit button is pressed. RIght now all answers are being checked as soon as next button is  pressed */}
                  <button
                    className="btn btn-success pull-right"
                    onClick={this.next.bind(this)}
                  >
                    Next Question <i className="fa fa-angle-double-right" />
                  </button>
                </label>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default QuizQuestions;
