import React, { Component } from "react";
// import Navbar from "react-bootstrap/Navbar";
// import Nav from "react-bootstrap/Nav";
// import { Link } from "react-router-dom";
import ReactPlayer from "react-player";
import Table from "react-bootstrap/Table";
import "./PackageVideo.css";

class PackageVideo extends Component {
  state = {
    data: null,
    packageName: null,
    videoUrl: ""
  };

  videoURLHandler = url => {
    if (url) {
      this.setState({ videoUrl: url });
    }
  };

  render() {
    var content = null;
    var videoUrl = null;
    var video = null;
    if (this.state.videoUrl) {
      videoUrl = this.state.videoUrl;
      video = (
        <ReactPlayer url={videoUrl} controls width="60%" height="400px" />
      );
      console.log("videoUrl", videoUrl);
    }

    content = this.props.data.content.map((key, index) => {
      return (
        <Table bordered hover key={index}>
          {Object.keys(key).map((d, index) => {
            return (
              <tbody key={d + index}>
                <tr>
                  <th>Title : {d}</th>
                </tr>

                {key[d].map((a, index) => {
                  return (
                    <tr key={Object.keys(a) + index}>
                      <td
                        onClick={() => this.videoURLHandler(a[Object.keys(a)])}
                      >
                        Video title : {Object.keys(a)}
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            );
          })}
        </Table>
      );
    });

    return (
      <div className="videoContainer">
        <div className="video">{video}</div>
        <div className="sidebar">{content}</div>
      </div>
    );
  }
}

export default PackageVideo;
