import React, { Component } from "react";
import "./PackageOverview.css";
import { API } from "../../config";
import { Button } from "react-bootstrap";

class PackageOverview extends Component {
  state = { discountCoupon: null, comment: "", userCoupon: "" };

  subscribeCourseHandler = data => {
    //Need this method to change subscribed property of course to true after successful payment
    fetch(`${API}/data`, {
      method: "POST",
      mode: "cors",
      cache: "no-cache",
      credentials: "same-origin",
      headers: {
        "Content-Type": "application/json"
      },
      redirect: "follow",
      referrer: "no-referrer",
      body: JSON.stringify(data)
    }).then(response => response.json());
  };

  submitCommentHandler = comment => {
    //Need this method to add comment to the comment section of the course along with userName
    fetch(`${API}/data`, {
      method: "POST",
      mode: "cors",
      cache: "no-cache",
      credentials: "same-origin",
      headers: {
        "Content-Type": "application/json"
      },
      redirect: "follow",
      referrer: "no-referrer",
      body: JSON.stringify(comment)
    }).then(response => response.json());
  };

  applyUserCouponHandler = couponCode => {
    //Need this method to replace/add coupon to the user
    fetch(`${API}/data`, {
      method: "POST",
      mode: "cors",
      cache: "no-cache",
      credentials: "same-origin",
      headers: {
        "Content-Type": "application/json"
      },
      redirect: "follow",
      referrer: "no-referrer",
      body: JSON.stringify(couponCode)
    }).then(response => response.json());
  };

  inputChangeHandler = event => {
    this.setState({ comment: event.target.value });
  };

  userCouponHandler = event => {
    this.setState({ userCoupon: event.target.value });
  };

  componentDidMount() {
    if (this.props.data.discountCoupon != null) {
      fetch(`${API}/coupons/${this.props.data.discountCoupon}`)
        .then(response => response.json())
        .then(data => {
          this.setState({ discountCoupon: data });
        });
    }
  }

  render() {
    var courseDetails = this.props.data;
    var overview = null;
    var cost = null;
    var reviews = null;
    var subscriptionDetails = null;
    var subscribeButton = null;
    var userCoupons = null;

    const getTodayDate = () => {
      var today = new Date();
      return (
        <span>
          {today.getDate()}/{today.getMonth() + 1}/{today.getFullYear()}
        </span>
      );
    };
    const getSubscriptionEndDate = days => {
      var date = new Date();
      var last = new Date(date.getTime() + days * 24 * 60 * 60 * 1000);
      var endingDay = last.getDate();
      var endingMonth = last.getMonth() + 1;
      var endYear = last.getFullYear();

      return (
        <span>
          {endingDay}/{endingMonth}/{endYear}
        </span>
      );
    };

    overview = (
      <div>
        <h1>{courseDetails.title}</h1>
        <h3>Course Description</h3>
        <h4>{courseDetails.description}</h4>
      </div>
    );

    if (courseDetails) {
      var discountedPrice = null;
      var discountPercentage = 0;
      if (this.state.discountCoupon) {
        discountPercentage = this.state.discountCoupon.discountPercentage;
      }
      if (this.state.discountCoupon != null) {
        discountedPrice =
          courseDetails.cost - (courseDetails.cost * discountPercentage) / 100;
        cost = (
          <div>
            <h4 className="strikethrough">
              Package Cost : {courseDetails.cost}
            </h4>
            <h4>Discount : {discountPercentage}%</h4>
            <h4>Final Price : Rs. {discountedPrice}</h4>
            <h4>You save : Rs. {courseDetails.cost - discountedPrice}</h4>
          </div>
        );
      } else {
        cost = (
          <div>
            <h4>Package Cost : {courseDetails.cost}</h4>
          </div>
        );
      }
    }

    if (courseDetails.reviews) {
      reviews = courseDetails.reviews.map(review => {
        return (
          <div key={review.id}>
            <div>
              {review.reviewer} : {review.title} : Rating : {review.rating}
            </div>
          </div>
        );
      });
    }

    if (courseDetails.subscriptionPeriod) {
      subscriptionDetails = (
        <div>
          Subscription valid from : {getTodayDate()} to{" "}
          {getSubscriptionEndDate(courseDetails.subscriptionPeriod)}
        </div>
      );
    }

    userCoupons = (
      <div>
        Enter coupon code
        <input
          type="text"
          placeholder="Enter Coupon"
          value={this.state.userCoupon}
          onChange={this.userCouponHandler}
        />
        <Button
          onClick={this.applyUserCouponHandler.bind(
            this,
            this.state.userCoupon
          )}
        >
          Apply Coupon
        </Button>
      </div>
    );

    subscribeButton = (
      <Button onClick={this.subscribeCourseHandler.bind(this, this.props.data)}>
        Subscribe
      </Button>
    );

    return (
      <div>
        <div>{overview}</div>
        <div>{cost}</div>
        <div>{subscriptionDetails}</div>
        <div>{subscribeButton}</div>
        <div>{userCoupons}</div>
        <div>
          <h4>Comments</h4>
        </div>
        <div>
          <input
            type="text"
            placeholder="Enter your comment"
            onChange={this.inputChangeHandler}
            value={this.state.comment}
          />
          <Button
            onClick={this.submitCommentHandler.bind(this, this.state.comment)}
          >
            Submit
          </Button>
          {reviews}
        </div>
      </div>
    );
  }
}

export default PackageOverview;
