import React, { Component } from "react";
import Jumbotron from "react-bootstrap/Jumbotron";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import { API } from "../../config";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import { Link } from "react-router-dom";

import NavigationBar from "../NavigationBar/NavigationBar";

class Help extends Component {
  state = {
    data: null,
    topic: null,
    emailAddress: null,
    name: null,
    message: null
  };

  componentDidMount() {
    fetch(`${API}/TathGatDetails`)
      .then(response => response.json())
      .then(data => {
        this.setState({ data });
      });
  }

  emailChangeHandler = event => {
    event.preventDefault();
    this.setState({ emailAddress: event.target.value });
  };

  nameChangeHandler = event => {
    event.preventDefault();
    this.setState({ name: event.target.value });
  };

  messageChangeHandler = event => {
    event.preventDefault();
    this.setState({ message: event.target.value });
  };

  topicSelectHandler = event => {
    this.setState({ topic: event.target.value });
  };

  sendQueryHandler = () => {
    return fetch(`${API}/sendQuery`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      },
      body:
        '{"name" : "' +
        this.state.name +
        '", "email" : "' +
        this.state.emailAddress +
        '", "topic" : "' +
        this.state.topic +
        '", "message" : "' +
        this.state.message +
        '"}'
    })
      .then(response => {
        const res = response.json();
        return res;
      })
      .catch(err => {
        console.log(err);
      });
  };

  render() {
    var content = null;
    var message = null;
    var FAQText = null;
    var data = this.state.data;
    var topic = this.state.topic;
    if (topic) {
      FAQText = (
        <p>Please checkout FAQs about {topic} before sending the message </p>
      );
    }
    if (data) {
      content = (
        <Jumbotron>
          <h1>Contact Us</h1>
          <p>Address : {data.address}</p>
          <p>Email Address : {data.emailAddress}</p>
          <p>Customer Care: {data.contactNumber}</p>
        </Jumbotron>
      );
    }

    if (data) {
      message = (
        <Form onSubmit={this.sendQueryHandler}>
          <Form.Group controlId="formBasicEmail">
            <Form.Label>Email</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter your email"
              onChange={this.emailChangeHandler}
              value={this.state.emailAddress}
            />
          </Form.Group>
          <Form.Group controlId="formBasicText">
            <Form.Label>Name</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter your name"
              onChange={this.nameChangeHandler}
              value={this.state.name}
            />
          </Form.Group>
          <Form.Group controlId="formBasicText">
            <Form.Label>Topic</Form.Label>
            <Form.Control
              as="select"
              onChange={this.topicSelectHandler.bind(this)}
              value={this.state.topic}
            >
              <option></option>
              <option>Subscription</option>
              <option>Renewal</option>
              <option>Partner with TathGat</option>
              <option>Classroom Programs</option>
              <option>Others</option>
            </Form.Control>
          </Form.Group>
          <Form.Group>
            <Form.Text>{FAQText}</Form.Text>
          </Form.Group>
          <Form.Group controlId="formBasicText">
            <Form.Label>Message</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter your message"
              onChange={this.messageChangeHandler}
              value={this.state.message}
            />
          </Form.Group>
          <Button variant="primary" type="submit">
            Ask us
          </Button>
        </Form>
      );
    }

    return (
      <div>
        <div>
          <NavigationBar />
        </div>
        <div>
          <Navbar bg="light" expand="lg">
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav className="mr-auto">
                <Nav.Link>
                  <Link
                    to="/FAQs"
                    style={{ textDecoration: "none", color: "black" }}
                  >
                    FAQs
                  </Link>
                </Nav.Link>
                <Nav.Link>
                  <Link
                    to="/help"
                    style={{ textDecoration: "none", color: "black" }}
                  >
                    Contact Us
                  </Link>
                </Nav.Link>
              </Nav>
            </Navbar.Collapse>
          </Navbar>
        </div>
        <div>{content}</div>
        <div className="container">{message}</div>
      </div>
    );
  }
}

export default Help;
