import React, { Component } from "react";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";

class CreatorDashboard extends Component {
  state = {
    creatingNewPackage: true,
    packageName: "",
    packageDescription: "",
    thumbnailURL: "",
    packageCost: 0,
    discountCoupon: "",
    subscriptionPeriod: 0,
    creatingNewModule: false,
    moduleName: "",
    creatingChapters: false,
    chapterName: "",
    inputs: ["input-0"]
  };

  check = () => {
    console.log("Package", this.package);
  };

  submitPackageNameHandler = event => {
    event.preventDefault();
    this.setState({ creatingNewModule: true });
  };

  savePackageNameHandler = event => {
    event.preventDefault();
    this.setState({ packageName: event.target.value });
  };

  savePackageDescriptionHandler = event => {
    event.preventDefault();
    this.setState({ packageDescription: event.target.value });
  };

  saveThumbnailURLHandler = event => {
    event.preventDefault();
    this.setState({ thumbnailURL: event.target.value });
  };

  savepackageCostHandler = event => {
    event.preventDefault();
    this.setState({ packageCost: event.target.value });
  };

  saveDiscountCouponHandler = event => {
    event.preventDefault();
    this.setState({ discountCoupon: event.target.value });
  };

  saveSubscriptionPeriodHandler = event => {
    event.preventDefault();
    this.setState({ subscriptionPeriod: event.target.value });
  };
  submitModuleNameHandler = event => {
    event.preventDefault();
    this.setState({ creatingChapters: true });
  };

  saveModuleNameHandler = event => {
    event.preventDefault();
    this.setState({ moduleName: event.target.value });
  };

  saveChapterNumberHandler = event => {
    event.preventDefault();
  };

  addNewChapterHandler = () => {
    return this.state.chapter.concat(
      <div>
        <Form.Group controlId="formBasicText">
          <Form.Label>Enter chapter name</Form.Label>
          <Form.Control type="text" placeholder="Enter chapter name" />
        </Form.Group>
        <Form.Group controlId="formBasicText">
          <Form.Label>Enter chapter URL</Form.Label>
          <Form.Control type="text" placeholder="Enter chapter URL" />
        </Form.Group>
      </div>
    );
  };

  data = "";
  package = "";

  addChaptersHandler = event => {
    event.preventDefault();
    var chapterContent = "";
    for (var i = 0; i < event.target.length - 3; i += 2) {
      console.log(event.target[i].value);
      if (i < event.target.length - 4) {
        chapterContent +=
          '{"' +
          event.target[i].value +
          '":"' +
          event.target[i + 1].value +
          '"},';
      }

      if (i === event.target.length - 4) {
        chapterContent +=
          '{"' +
          event.target[i].value +
          '":"' +
          event.target[i + 1].value +
          '"}';
        this.finishModule(chapterContent);
      }
    }
  };

  finishModule(chapterContent) {
    this.data +=
      '{"' + this.state.moduleName + '":[  ' + chapterContent + "]},";
    this.setState({ creatingChapters: false });
  }

  appendInput() {
    var newInput = `input-${this.state.inputs.length}`;
    this.setState(prevState => ({
      inputs: prevState.inputs.concat([newInput])
    }));
  }

  finishPackageHandler = () => {
    this.package +=
      '{ "id" : "' +
      this.state.packageName +
      '","title" : "' +
      this.state.packageName +
      '", "description" : "' +
      this.state.packageDescription +
      '", "imageUrl" :  "' +
      this.state.thumbnailURL +
      '", "subscribed" : true , "cost" : ' +
      this.state.packageCost +
      ',"discountCoupon" : "' +
      this.state.discountCoupon +
      '", "subscriptionPeriod" : ' +
      this.state.subscriptionPeriod +
      ',"content" : [' +
      this.data.substring(0, this.data.length - 1) +
      "] }";
    fetch("http://localhost:3000/courses", {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      },
      body: this.package
    })
      .then(function(response) {
        return response.json();
      })
      .then(function(data) {
        console.log(data);
      });
  };

  render() {
    var checkButton = <Button onClick={this.check}>Check</Button>;
    var packageName = null;
    var moduleName = null;
    var chapters = null;

    //Package Name Handler
    if (this.state.creatingNewPackage) {
      packageName = (
        <Form onSubmit={this.submitPackageNameHandler}>
          <Form.Group controlId="formBasicText">
            <Form.Label>Package name</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter package name"
              onChange={this.savePackageNameHandler}
              value={this.state.packageName}
            />
          </Form.Group>
          <Form.Group controlId="formBasicText">
            <Form.Label>Package description</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter package description"
              onChange={this.savePackageDescriptionHandler}
              value={this.state.packageDescription}
            />
          </Form.Group>
          <Form.Group controlId="formBasicText">
            <Form.Label>Course thumbnail URL</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter course thumbnail URL"
              onChange={this.saveThumbnailURLHandler}
              value={this.state.thumbnailURL}
            />
          </Form.Group>
          <Form.Group controlId="formBasicText">
            <Form.Label>Package Cost</Form.Label>
            <Form.Control
              type="number"
              placeholder="Enter package cost"
              onChange={this.savepackageCostHandler}
              value={this.state.packageCost}
            />
          </Form.Group>
          <Form.Group controlId="formBasicText">
            <Form.Label>Discount Coupon</Form.Label>
            <Form.Control
              type="text"
              placeholder="Select discount coupon"
              onChange={this.saveDiscountCouponHandler}
              value={this.state.discountCoupon}
            />
          </Form.Group>
          <Form.Group controlId="formBasicText">
            <Form.Label>Subscription Period</Form.Label>
            <Form.Text>(In terms of number of days)</Form.Text>
            <Form.Control
              type="number"
              placeholder="Select discount coupon"
              onChange={this.saveSubscriptionPeriodHandler}
              value={this.state.subscriptionPeriod}
            />
          </Form.Group>
          <Button variant="primary" type="submit">
            Create package
          </Button>
          <Button variant="primary" onClick={this.finishPackageHandler}>
            Finish package
          </Button>
        </Form>
      );
    }

    //Module Name handler
    if (this.state.creatingNewModule) {
      moduleName = (
        <Form onSubmit={this.submitModuleNameHandler}>
          <Form.Group controlId="formBasicText">
            <Form.Label>Enter module name</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter module name"
              onChange={this.saveModuleNameHandler}
              value={this.state.moduleName}
            />
          </Form.Group>
          <Button variant="primary" type="submit">
            Create module
          </Button>
        </Form>
      );
    }

    //Chapter Handler
    if (this.state.creatingChapters) {
      chapters = (
        <div>
          <Form onSubmit={this.addChaptersHandler}>
            <div id="dynamicInput">
              {this.state.inputs.map(input => (
                <div key={input}>
                  <Form.Group controlId="formBasicText">
                    <Form.Label>Enter chapter name</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter chapter name"
                    />
                  </Form.Group>
                  <Form.Group controlId="formBasicText">
                    <Form.Label>Enter chapter URL</Form.Label>
                    <Form.Control type="text" placeholder="Enter chapter URL" />
                  </Form.Group>
                </div>
              ))}
            </div>
            <Button variant="primary" onClick={() => this.appendInput()}>
              Add new chapter
            </Button>
            <Button variant="primary" type="submit">
              Finish module
            </Button>
          </Form>
        </div>
      );
    }
    return (
      <div className="container">
        <div>{checkButton}</div>
        <div>{packageName}</div>
        <div>{moduleName}</div>
        <div>{chapters}</div>
      </div>
    );
  }
}

export default CreatorDashboard;
