import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import { signin, authenticate } from "../../auth/serverMethods";
// import { API } from "../../config";
// import SocialMediaLogin from "../SocialMediaLogin/SocialMediaLogin";

class Signin extends Component {
  state = {
    email: "admin@gmail.com",
    password: "password",
    contactNumber: "",
    error: "",
    loading: false,
    redirectToReferrer: false,
    userId: ""
  };

  signInForm = () => {
    return (
      <form>
        <div className="form-ground">
          <label className="text-muted">Email</label>
          <input
            type="email"
            className="form-control"
            onChange={this.emailChangeHandler}
            value={this.state.email}
          />
        </div>
        <div className="form-ground">
          <label className="text-muted">Password</label>
          <input
            type="password"
            className="form-control"
            onChange={this.passwordChangeHandler}
            value={this.state.password}
          />
        </div>
        <button onClick={this.clickSubmit} className="btn btn-primary">
          Submit
        </button>
      </form>
    );
  };

  emailChangeHandler = event => {
    this.setState({ email: event.target.value, error: false });
  };

  passwordChangeHandler = event => {
    this.setState({ password: event.target.value, error: false });
  };

  clickSubmit = event => {
    event.preventDefault();
    this.setState({ error: false, loading: true });
    const data = signin();
    authenticate(data, () => {
      this.setState({ redirectToReferrer: true, userId: data.id });
    });
  };

  showError = () => {
    return (
      <div
        className="alert alert-danger"
        style={{ display: this.state.error ? "" : "none" }}
      >
        {this.state.error}
      </div>
    );
  };

  showLoading = () => {
    return (
      this.state.loading && (
        <div className="alert alert-info">
          <h2>Loading</h2>
        </div>
      )
    );
  };

  redirectUser = () => {
    if (this.state.redirectToReferrer) {
      switch (this.state.userId) {
        case "admin":
          return <Redirect to="/admin/dashboard" />;
        case "uploader":
          return <Redirect to="/uploader/dashboard" />;
        case "creator":
          return <Redirect to="/creator/dashboard" />;
        case "accountant":
          return <Redirect to="/accountant/dashboard" />;
        default:
          return <Redirect to="/" />;
      }
    }
  };

  render() {
    return (
      <div
        title="SignUp"
        description="Sign up to ecommerce app"
        className="container col-md-8 offset-md-2"
      >
        {this.showError()}
        {this.showLoading()}
        {this.signInForm()}
        {this.redirectUser()}
        {/* <SocialMediaLogin /> */}
      </div>
    );
  }
}

export default Signin;
