import React, { Component } from "react";
import { Card, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import { API } from "../../config";
import "./AdditionalBanner.css";

class MainBanner extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: null
    };
  }
  componentDidMount() {
    fetch(`${API}/courses`)
      .then(response => response.json())
      .then(data => this.setState({ data }));
  }

  render() {
    const mySubscriptions = this.state.data;
    var content = null;
    if (mySubscriptions) {
      content = mySubscriptions.map(mySubscription => {
        if (!mySubscription.subscribed) {
          return (
            <div className="CourseCard" key={mySubscription.id}>
              <img
                className="CardImage"
                src={require("../../assets/course-image-1.jpg")} //need to set to dynamic url
                alt="Course Icon"
              />
              <Card.Title>
                <b>{mySubscription.title}</b>
              </Card.Title>
              <div className="CardText">{mySubscription.description}</div>
              <div>
                <Link
                  to={"/packages/" + mySubscription.id}
                  style={{ textDecoration: "none", color: "black" }}
                >
                  <Button variant="primary" className="CourseButton">
                    Continue
                  </Button>
                </Link>
              </div>
            </div>
          );
        } else {
          return null;
        }
      });
    }
    return <div className="Course">{content}</div>;
  }
}
export default MainBanner;
