import React, { Component } from "react";
import { Link } from "react-router-dom";
import { signup } from "../../auth/serverMethods";
// import SocialMediaLogin from "../SocialMediaLogin/SocialMediaLogin";

class Signup extends Component {
  state = {
    name: "",
    email: "",
    password: "",
    contactNumber: "",
    error: "",
    success: false
  };

  signUpForm = () => {
    return (
      <form>
        <div className="form-ground">
          <label className="text-muted">Name</label>
          <input
            type="text"
            className="form-control"
            onChange={this.nameChangeHandler}
            value={this.state.name}
          />
        </div>
        <div className="form-ground">
          <label className="text-muted">Email</label>
          <input
            type="email"
            className="form-control"
            onChange={this.emailChangeHandler}
            value={this.state.email}
          />
        </div>
        <div className="form-ground">
          <label className="text-muted">Password</label>
          <input
            type="password"
            className="form-control"
            onChange={this.passwordChangeHandler}
            value={this.state.password}
          />
          <div className="form-ground">
            <label className="text-muted">Contact number</label>
            <input
              type="tel"
              className="form-control"
              onChange={this.contactChangeHandler}
              value={this.state.contactNumber}
            />
          </div>
        </div>
        <button onClick={this.clickSubmit} className="btn btn-primary">
          Submit
        </button>
      </form>
    );
  };

  nameChangeHandler = event => {
    this.setState({ name: event.target.value, error: false });
  };

  emailChangeHandler = event => {
    this.setState({ email: event.target.value, error: false });
  };

  passwordChangeHandler = event => {
    this.setState({ password: event.target.value, error: false });
  };

  contactChangeHandler = event => {
    this.setState({ contactNumber: event.target.value, error: false });
  };

  clickSubmit = event => {
    event.preventDefault();
    this.setState({ error: false });
    signup({
      name: this.state.name,
      email: this.state.email,
      password: this.state.password,
      contactNumber: this.state.contactNumber
    }).then(data => {
      if (data.error) {
        this.setState({ error: data.error, success: false });
      } else {
        this.setState({
          name: "",
          email: "",
          password: "",
          contactNumber: "",
          error: "",
          success: true
        });
      }
    });
  };

  showError = () => {
    return (
      <div
        className="alert alert-danger"
        style={{ display: this.state.error ? "" : "none" }}
      >
        {this.state.error}
      </div>
    );
  };

  showSuccess = () => {
    return (
      <div
        className="alert alert-info"
        style={{ display: this.state.success ? "" : "none" }}
      >
        New Account is created. Please{" "}
        <Link to="/login" exact>
          sign in.
        </Link>
      </div>
    );
  };

  render() {
    return (
      <div
        title="SignUp"
        description="Sign up to ecommerce app"
        className="container col-md-8 offset-md-2"
      >
        {this.showError()};{this.showSuccess()}
        {this.signUpForm()}
        {/* <SocialMediaLogin /> */}
      </div>
    );
  }
}

export default Signup;
