import React, { Component } from "react";
// import { API } from "../config";
import coverPic1 from "../assets/course-image-1.jpg";
import coverPic2 from "../assets/course-image-2.jpg";
import MCQs from "./MCQs";

class TestList extends Component {
  constructor() {
    super();
    this.state = {
      quiz_list: null,
      quiz_info: null,
      saveSelectedQuizObj: null,
      renderSelectedTestObj: false,
      renderSelectedTestParts: false,
      renderMCQs: false,
      currentTestIndex: null,
      min: null,
      sec: null,
      timerOn: false,
      stack: []
    };
    this.minute = 0;
    this.second = 1;
    this.timeStart = null;
    this.back = this.back.bind(this);
    this.backToDashboard = this.backToDashboard.bind(this);
  }

  back() {
    this.setState({ renderSelectedTestObj: false });
  }

  backToDashboard(param) {
    this.setState({ renderMCQs: param });
  }

  // componentDidMount() {
  //   fetch(`${API}/quiz_list`)
  //     .then(response => response.json())
  //     .then(data => {
  //       this.setState({ quiz_list: data });
  //     });

  //   fetch(`${API}/quiz_info`)
  //     .then(response => response.json())
  //     .then(data => {
  //       this.setState({ quiz_info: data });
  //     });
  // }

  updateQuizInfoState(index) {
    const { quiz_info } = this.state;
    this.setState({
      saveSelectedQuizObj: quiz_info[index],
      renderSelectedTestObj: true
    });
  }

  renderQuizList() {
    const { quiz_list } = this.state;
    if (quiz_list) {
      return (
        <div>
          <h2>Dashboard</h2>

          <div className="row">
            {quiz_list.map((qList, index) => {
              return (
                <div className="col-md-4" key={`${qList}_${index}`}>
                  <div className="card" style={{ width: "18rem" }}>
                    <img className="card-img-top" src={coverPic1} alt="Card" />
                    <div className="card-body">
                      <h5 className="card-title">{qList.quizName} Quiz</h5>
                      <p className="card-text">
                        Test your skills of {qList.quizName} by taking this
                        small quiz. It has {qList.tests} tests.
                      </p>
                      <button
                        className="btn btn-info"
                        onClick={this.updateQuizInfoState.bind(this, index)}
                      >
                        Next <i className=" fa fa-paper-plane" />
                      </button>
                      {/* <button className="btn btn-primary" >Next <i class=" fa fa-paper-plane"></i></button> */}
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      );
    } else {
      return null;
    }
  }

  renderQuizInfo() {
    const { saveSelectedQuizObj } = this.state;
    return (
      <div>
        <h2>{saveSelectedQuizObj.quizName} Quiz</h2>

        <div className="row">
          {saveSelectedQuizObj.tests.map((test, i) => {
            return (
              <div
                className="col-md-4"
                key={`${saveSelectedQuizObj.quizName}_${test.name}`}
              >
                <div className="card" style={{ width: "18rem" }}>
                  <img className="card-img-top" src={coverPic2} alt="" />
                  <div className="card-body">
                    <h5 className="card-title">{test.name}</h5>
                    <p className="card-text">
                      Total Questions: {test.totalQuestions}
                    </p>
                    <p>Total Time: {test.totalTime / 60} Minutes</p>
                    <button
                      className="btn btn-success"
                      onClick={() => {
                        this.setState({
                          //   renderMCQs: true,
                          renderSelectedTestParts: true,
                          currentTestIndex: i,
                          renderSelectedTestObj: false
                        });
                      }}
                    >
                      Start Quiz {i + 1} <i className=" fa fa-paper-plane" />
                    </button>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
        <br />
        <button className="btn btn-secondary" onClick={this.back}>
          Back <i className="fa fa-backward" />
        </button>
      </div>
    );
  }

  startTimer = () => {
    this.setState({
      timerOn: true,
      renderMCQs: true,
      renderSelectedTestObj: false,
      renderSelectedTestParts: false
    });

    this.timer();
  };

  timer() {
    // this.timeStart = setInterval(() => {
    //   this.setState({
    //     min: 0,
    //     sec: 500
    //   });
    //   this.second--;
    //   if (this.second === 0) {
    //     this.second = 60;
    //     this.minute--;
    //     this.setState({
    //       sec: this.second,
    //       min: this.minute
    //     });
    //     if (this.minute < 0) {
    //       this.setState({
    //         renderMCQs: false,
    //         renderSelectedTestParts: true,
    //         timerOn: false
    //       });
    //       this.minute = 0;
    //       this.second = 500;
    //     }
    //   }
    // }, 1000);
  }

  renderQuizParts() {
    const { saveSelectedQuizObj, currentTestIndex } = this.state;
    // this.stack.push(saveSelectedQuizObj.tests[currentTestIndex].parts);
    console.log("Timer started");
    console.log(saveSelectedQuizObj.tests[currentTestIndex].parts);
    var part = saveSelectedQuizObj.tests[currentTestIndex].parts.pop();
    return (
      part && (
        <div>
          <div className="col-md-4" key={part.partName}>
            <div className="card" style={{ width: "18rem" }}>
              <img className="card-img-top" src={coverPic2} alt="" />
              <div className="card-body">
                <h5 className="card-title">{part.partName}</h5>
                <p className="card-text">Total questions : {part.questions}</p>
                <p className="card-text">Time alloted : {part.time}</p>
              </div>
            </div>
            <button
              className="btn btn-success"
              onClick={() => {
                this.startTimer();
              }}
            >
              Start Quiz <i className=" fa fa-paper-plane" />
            </button>
          </div>
        </div>
      )
    );
  }

  render() {
    const {
      renderSelectedTestObj,
      renderMCQs,
      saveSelectedQuizObj,
      currentTestIndex,
      renderSelectedTestParts
    } = this.state;
    var content = null;
    if (renderSelectedTestObj) {
      content = this.renderQuizInfo();
    } else {
      if (renderSelectedTestParts) {
        content = <div className="row">{this.renderQuizParts()}</div>;
      } else if (renderMCQs) {
        content = (
          <MCQs
            currentQuesObj={saveSelectedQuizObj}
            currentTestIndex={currentTestIndex}
            backToDashboard={this.backToDashboard}
          />
        );
      } else {
        content = this.renderQuizList();
      }
    }
    return (
      <div>
        <div>{content}</div>
      </div>
    );
  }
}

export default TestList;
