import { API } from "../config";

export const signup = user => {
  return fetch(`${API}/users`, {
    method: "POST",
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json"
    },
    body: JSON.stringify(user)
  })
    .then(response => {
      const res = response.json();
      return res;
    })
    .catch(err => {
      console.log(err);
    });
};

