import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import { signout, isAuthenticated } from "../../auth/serverMethods";
import { API } from "../../config";

import "./NavigationBar.css";

import Logo from "../../assets/logo192.png";
import Dropdown from "react-bootstrap/Dropdown";
import DropdownButton from "react-bootstrap/DropdownButton";
import Cart from "../../assets/ShoppingCart.png";
import BellIcon from "../../assets/Bell.png";
import Button from "react-bootstrap/Button";

class NavigationBar extends Component {
  state = {
    courses: null
  };

  componentDidMount() {
    fetch(`${API}/courses`)
      .then(response => response.json())
      .then(data => this.setState({ courses: data }));
  }

  render() {
    const mySubscriptions = this.state.courses;
    var content = null;
    if (mySubscriptions) {
      content = mySubscriptions.map(mySubscription => {
        if (mySubscription.subscribed) {
          return (
            <Dropdown.Item key={mySubscription.id}>
              {mySubscription.title}
            </Dropdown.Item>
          );
        } else {
          return null;
        }
      });
    }

    const navigationContent = (
      <div className="Content">
        <img src={Logo} alt="Logo" className="Logo" />
        <div className="Input">
          <input
            type="text"
            className="form-control"
            placeholder="Search for anything"
          />
        </div>
        <span id="Teach">Teach on Tathagat</span>

        <span id="DropdownButton">
          <DropdownButton variant="primary" title="MySubscriptions">
            {content}
            <Dropdown.Item>Show all courses</Dropdown.Item>
          </DropdownButton>
        </span>
        <div id="Cart">
          <img src={Cart} alt="Cart" className="Logo" />
        </div>
        {/* <div id="Cart">
          <img src={BellIcon} alt="Cart" className="Logo" />
        </div> */}
        {!isAuthenticated() && (
          <div>
            <Button id="Login" variant="outline-light">
              <Link
                to="login"
                style={{ textDecoration: "none", color: "white" }}
              >
                Login
              </Link>
            </Button>
            <Button id="Signup" variant="outline-light">
              <Link
                to="signUp"
                style={{ textDecoration: "none", color: "white" }}
              >
                SignUp
              </Link>
            </Button>
          </div>
        )}
        {isAuthenticated() && (
          <DropdownButton id="Signup" title="Profile">
            <Dropdown.Item>My Profile</Dropdown.Item>
            <Dropdown.Item>
              <Link
                to="/referral"
                style={{ textDecoration: "none", color: "black" }}
              >
                Referral
              </Link>
            </Dropdown.Item>
            <Dropdown.Item>
              <Link
                to="/help"
                style={{ textDecoration: "none", color: "black" }}
              >
                Help
              </Link>
            </Dropdown.Item>
            <Dropdown.Item
              variant="outline-light"
              onClick={() =>
                signout(() => {
                  this.props.history.push("/");
                })
              }
              style={{
                textDecoration: "none",
                cursor: "pointer"
              }}
            >
              SignOut
            </Dropdown.Item>
          </DropdownButton>
        )}
      </div>
    );
    return <React.Fragment>{navigationContent}</React.Fragment>;
  }
}

export default withRouter(NavigationBar);
