import React, { Component } from "react";
import { API } from "../../config";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import Container from "react-bootstrap/Container";
import Image from "react-bootstrap/Image";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";

import MailIcon from "../../assets/Mail.png";
import MessageIcon from "../../assets/Message.png";
import WhatsappIcon from "../../assets/Whatsapp.png";

class Referral extends Component {
  state = {
    currentUser: null,
    initiatingRedeem: false,
    referralAmount: 0,
    accountNumber: "",
    accountHolderName: "",
    accountIFSCCode: ""
  };
  componentDidMount() {
    fetch(`${API}/currentUser`)
      .then(response => response.json())
      .then(data => {
        this.setState({ currentUser: data });
      });
  }

  sendReferralEmail = () => {
    return fetch(`${API}/sendReferralEmail`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      },
      body:
        '{ "referralCode" : ' +
        JSON.stringify(this.state.currentUser.referralCode) +
        "}"
    })
      .then(response => {
        const res = response.json();
        return res;
      })
      .catch(err => {
        console.log(err);
      });
  };

  sendReferralMessage = () => {
    return fetch(`${API}/sendReferralMessage`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      },
      body:
        '{ "referralCode" : ' +
        JSON.stringify(this.state.currentUser.referralCode) +
        "}"
    })
      .then(response => {
        const res = response.json();
        return res;
      })
      .catch(err => {
        console.log(err);
      });
  };

  sendReferralWhatsappMessage = () => {
    return fetch(`${API}/sendReferralWhatsappMessage`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      },
      body:
        '{ "referralCode" : ' +
        JSON.stringify(this.state.currentUser.referralCode) +
        "}"
    })
      .then(response => {
        const res = response.json();
        return res;
      })
      .catch(err => {
        console.log(err);
      });
  };

  initiateRedeem = () => {
    console.log("initiating redeem");
    this.setState({ initiatingRedeem: true });
  };

  redeem = event => {
    event.preventDefault();
    return fetch(`${API}/redeemReferralAmount`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      },
      body:
        '{ "referralCode" : ' +
        JSON.stringify(this.state.currentUser.referralCode) +
        ',"referralAmount" : ' +
        this.state.currentUser.referralAmount +
        ',"accountNumber" : "' +
        this.state.accountNumber +
        '", "accountHolderName" : "' +
        this.state.accountHolderName +
        '", "IFSCCode": "' +
        this.state.accountIFSCCode +
        '"}'
    })
      .then(response => {
        const res = response.json();
        return res;
      })
      .catch(err => {
        console.log(err);
      });
  };

  accountNumberHandler = event => {
    this.setState({ accountNumber: event.target.value });
  };

  accountHolderNameHandler = event => {
    this.setState({ accountHolderName: event.target.value });
  };

  accountIFSCHandler = event => {
    this.setState({ accountIFSCCode: event.target.value });
  };

  render() {
    var content = null;
    var amount = null;
    var bankDetailForm = null;
    var user = this.state.currentUser;
    if (user) {
      content = (
        <Card
          style={{
            width: "60%",
            margin: "auto",
            alignItems: "center"
          }}
        >
          <Card.Body>
            <Card.Title>Referral Code</Card.Title>
            <Card.Text>{this.state.currentUser.referralCode}</Card.Text>
            <Container>
              <Row>
                <Col xs={4} md={4} className="h-25 d-inline-block">
                  <Image
                    src={MailIcon}
                    roundedCircle
                    onClick={this.sendReferralEmail}
                  />
                </Col>
                <Col xs={4} md={4}>
                  <Image
                    src={MessageIcon}
                    roundedCircle
                    onClick={this.sendReferralMessage}
                  />
                </Col>
                <Col xs={4} md={4}>
                  <Image
                    src={WhatsappIcon}
                    roundedCircle
                    onClick={this.sendReferralWhatsappMessage}
                  />
                </Col>
              </Row>
            </Container>
          </Card.Body>
        </Card>
      );
    }

    if (user) {
      amount = (
        <Card
          style={{
            width: "60%",
            margin: "auto",
            alignItems: "center"
          }}
        >
          <Card.Body>
            <Card.Title>Amount Earned</Card.Title>
            <Card.Text>{user.referralAmount}</Card.Text>
            <Button variant="primary" onClick={this.initiateRedeem}>
              Redeem
            </Button>
          </Card.Body>
        </Card>
      );
    }

    if (this.state.initiatingRedeem) {
      bankDetailForm = (
        <Form onSubmit={this.redeem}>
          <Form.Group controlId="formBasicNumber">
            <Form.Label>Account Number</Form.Label>
            <Form.Control
              type="Number"
              placeholder="Enter account number"
              onChange={this.accountNumberHandler}
              value={this.state.accountNumber}
              required
            />
          </Form.Group>

          <Form.Group controlId="formBasicText">
            <Form.Label>Account Holder Name</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter account holder name"
              onChange={this.accountHolderNameHandler}
              value={this.state.accountHolderName}
              required
            />
          </Form.Group>

          <Form.Group controlId="formBasicText">
            <Form.Label>IFSC code</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter account IFSC code"
              onChange={this.accountIFSCHandler}
              value={this.state.accountIFSCCode}
              required
            />
          </Form.Group>

          <Button variant="primary" type="submit">
            Redeem
          </Button>
        </Form>
      );
    }
    return (
      <div>
        <div>{content}</div>
        <div>{amount}</div>
        <div>{bankDetailForm}</div>
      </div>
    );
  }
}

export default Referral;
